<script>
import { mapGetters } from "vuex";

export default {
  name: "KoreaMap",
  computed: {
    ...mapGetters(["trophy", "trophyList"]),
  },
  data: () => ({
    info: false,
  }),
  methods: {
    toggleInfoOn() {
      this.info = true;
    },
    toggleInfoOff() {
      this.info = false;
    },
  },
};
</script>

<style>
.level0 {
  fill: #ccc;
  stroke-linejoin: round;
  stroke: #ffffff;
  stroke-width: 1;
}

.level1 {
  fill: #ffee57;
  stroke-linejoin: round;
  stroke: #ffffff;
  stroke-width: 1;
}

.level2 {
  fill: #d0ff6d;
  stroke-linejoin: round;
  stroke: #ffffff;
  stroke-width: 1;
}

.level3 {
  fill: #52cbff;
  stroke-linejoin: round;
  stroke: #ffffff;
  stroke-width: 1;
}

.level4 {
  fill: #8263ff;
  stroke-linejoin: round;
  stroke: #ffffff;
  stroke-width: 1;
}

.level5 {
  fill: #ff6262;
  stroke-linejoin: round;
  stroke: #ffffff;
  stroke-width: 1;
}

.TEXT {
  fill: #ffffff;
  font-size: 16;
  font-weight: normal;
  text-anchor: middle;
  alignment-baseline: middle;
}

.fade-enter-active {
  transition: opacity 0.5s;
}
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter {
  opacity: 0;
}
.fade-leave-to {
  opacity: 0;
}
</style>
